.OrderList-table-item-btn{
    cursor: pointer;
}

.OrderList-table-item{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}

.OrderList-table-item>div:nth-child(3) {
    flex: 0.4;
    width: 40px;
    height: 40px;
    text-align: start;
}

.OrderList-table-item>div{
    flex: 1;
    text-align: center;
    height: 40px;
    line-height: 40px;
    overflow: scroll;
    white-space: nowrap;
    padding: 0 10px;
}

.OrderList-table-item-checkBox{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 20px;
}
