.Home .pickDay{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.Home .pickDay-day{
    font-size: 18px;
    line-height: 32px;
}

.appBar-right-btn{
    color: #FFF !important;
    font-size: 16px !important;
    height: 50px !important;
    background-color: transparent !important;
}

.msgInput{
    padding: 0px 20px;
}

.Home-table{
    width: 90%;
    margin: 0 auto;
}

.Home-table-item{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}

.Home-table-item div:nth-child(1) {
    flex: 0.3;
    width: 40px;
    height: 40px;
    text-align: start;
}

.Home-table-item>div{
    flex: 1;
    text-align: center;
    height: 40px;
    line-height: 40px;
}

.Home-table-item-checkBox{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 20px;
}
