body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,body,#root {
  height: 100%;
}
.Nav{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-align: center;
}

.Nav li {
  text-align: center;
  flex: 1 1;
}

.App{
  display: flex;
  flex-direction: column;
  height: 100%;
  height-max: 100%;
}

.App-main{
  flex: 1 1;
  overflow: scroll;
}

.Nav .NavBtn{
  width: 100%;
  height: 56px !important;
}

.pickDay{

}


.My{
    display: flex;
    flex-direction: column;
    /*justify-content: space-around;*/
    height: 100%;
}

.Home .pickDay{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.Home .pickDay-day{
    font-size: 18px;
    line-height: 32px;
}

.appBar-right-btn{
    color: #FFF !important;
    font-size: 16px !important;
    height: 50px !important;
    background-color: transparent !important;
}

.msgInput{
    padding: 0px 20px;
}

.Home-table{
    width: 90%;
    margin: 0 auto;
}

.Home-table-item{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}

.Home-table-item div:nth-child(1) {
    flex: 0.3 1;
    width: 40px;
    height: 40px;
    text-align: start;
}

.Home-table-item>div{
    flex: 1 1;
    text-align: center;
    height: 40px;
    line-height: 40px;
}

.Home-table-item-checkBox{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 20px;
}

.HomePage{
    display: flex;
    flex-direction: column;
    height: 100%;
}

.HomePage-main {
    flex: 1 1;
    overflow: scroll;
}

.Nav{
    background: #FFFFFF;
    box-shadow: 0px 0px 3px 3px #EEE;
}

.Login-Main {
    margin-top: 50px;
    padding: 0px 20px;
}

.login-Btn{
    background-color: rgb(0, 188, 212) !important;
    color: #FFF !important;
}

.OrderList-table-item-btn{
    cursor: pointer;
}

.OrderList-table-item{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}

.OrderList-table-item>div:nth-child(3) {
    flex: 0.4 1;
    width: 40px;
    height: 40px;
    text-align: start;
}

.OrderList-table-item>div{
    flex: 1 1;
    text-align: center;
    height: 40px;
    line-height: 40px;
    overflow: scroll;
    white-space: nowrap;
    padding: 0 10px;
}

.OrderList-table-item-checkBox{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 20px;
}

body {margin:0; padding:0; font:12px/1.5 helvetica,arial,"Microsoft YaHei",\5b8b\4f53;}
div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,textarea,blockquote,p{padding:0; margin:0;}
table,td,tr,th{font-size:12px;}
ol,ul {list-style:none;}
li{list-style-type:none;}
img{vertical-align:top;border:0;max-width: 100%; height:auto;}
h1,h2,h3,h4,h5,h6{font-size:inherit; font-weight:normal;}
address,cite,code,em,th,i,var{font-weight:normal; font-style:normal;}
.clearfix{*zoom:1;}
.clearfix:after{display:block; overflow:hidden; clear:both; height:0; visibility:hidden; content:".";}
a:link,a:visited
{
    color:#000;
    text-decoration: none;
}
input
{
    outline: 0;
    outline: none;
}
.ellipsis
{
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}
.fl{float: left;}
.fr{float: right;}
.mt10{margin-top: 10px;}
.ml10{margin-left: 10px;}
.mb10{margin-top: 10px;}
.mr10{margin-left: 10px;}
.main-wrap{width: 1200px; margin:0 auto;}
body{
    background-color: #f5f5f5;
}
